.hide, .hidden {
  display: none;
}

.cat-info {
  gap: 30px;
  display: flex;
}

.cat-none {
  display: none;
}

h2 {
  color: #2f8355;
}

.breed-select {
  width: 600px;
  background-color: #f1f1f1;
  border: 1px solid #ccc;
  border-radius: 6px;
  padding: 12px;
}

.error {
  text-align: center;
  color: red;
  margin: 20px;
}

.error-none {
  display: none;
}

.hide-select {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  box-shadow: none;
  background: none;
  border: none;
  outline: none;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-radius: 50%;
  animation: animloader 1s linear infinite;
  display: inline-block;
  position: fixed;
  bottom: 50%;
  left: 50%;
}

@keyframes animloader {
  0% {
    box-shadow: inset -72px 0 #8bc983;
  }

  100% {
    box-shadow: inset 48px 0 #03310a;
  }
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.selector-wrapper {
  position: relative;
}

.selector-wrapper.hide:after {
  content: "";
  background-color: #fff;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

